angular.module("meanbaseApp").run(["$templateCache", function($templateCache) {$templateCache.put("extensions/gallery/gallery.html","<div class=\"gallery-container\">\n  <carousel interval=\"interval\">\n    <slide ng-repeat=\"image in images\" active=\"image.active\">\n      <img ng-src=\"{{image.modifiedurl}}\" style=\"margin:auto;\" fallback-src=\"http://placehold.it/900x300\">\n      <div class=\"carousel-caption\">\n        <h4>Slide {{$index}}</h4>\n        <p>{{image.alt}}</p>\n      </div>\n    </slide>\n  </carousel>\n  <div ng-if=\"editMode\" class=\"gallery-config\">\n    <button class=\"btn btn-block btn-primary gallery-find-images-button\" find-images-modal find-images-config=\"findImagesConfig\" ng-if=\"editMode\">Select Images</button>\n    <div class=\"form-group\">\n      <div class=\"input-group\">\n        <div class=\"input-group-addon\">\n          <span>Gallery Name</span>\n        </div>\n        <input type=\"text\" class=\"form-control\" placeholder=\"Gallery Slug\" ng-model=\"extension.config.slug\"> \n      </div>\n    </div>\n    <div class=\"form-group\">\n      <div class=\"input-group\">\n        <div class=\"input-group-addon\">\n          <span>Interval</span>\n        </div>\n        <input type=\"text\" class=\"form-control\" placeholder=\"3000\" ng-model=\"extension.config.interval\">\n      </div>\n    </div>\n    <!-- <div find-images-for=\"{{extension.config.slug}}\" multiple=\"true\" class=\"gallery-find-images\"></div> -->\n    <!-- <div find-images-modal find-images-config=\"config\" multiple=\"true\" class=\"gallery-find-images\"></div> -->\n  </div>\n</div>");
$templateCache.put("extensions/gallery/index.html","<gallery></gallery>");}]);


